<template>
  <div>
    <a-transfer
      class="tree-transfer"
      :data-source="dataSource"
      :target-keys="targetKeys"
      :render="(item) => item.title"
      :show-select-all="false"
      @change="onChange"
    >
      <template #children="{ direction, selectedKeys, onItemSelect }">
        <a-tree
          v-if="direction === 'left'"
          checkStrictly
          blockNode
          checkable
          defaultExpandAll
          v-model:checkedKeys="checkedKeysone"
          :checkedKeys="[...selectedKeys]"
          :treeData="treeData"
          @check="
            (_, props) => {
              onChecked(
                _,
                props,
                [...selectedKeys, ...targetKeys],
                onItemSelect
              );
            }
          "
          @select="
            (_, props) => {
              onChecked(
                _,
                props,
                [...selectedKeys, ...targetKeys],
                onItemSelect
              );
            }
          "
        />
        <a-tree
          v-if="direction === 'right'"
          checkStrictly
          blockNode
          checkable
          defaultExpandAll
          v-model:checkedKeys="checkedKey"
          :treeData="rightTreeData"
          @check="
            (_, props) => {
              onCheckedRight(
                _,
                props,
                [...selectedKeys, ...targetKeys],
                onItemSelect
              );
            }
          "
        />
        <!-- @select="
            (_, props) => {
              onCheckedRight(
                _,
                props,
                [...selectedKeys, ...targetKeys],
                onItemSelect
              );
            }
          " -->
      </template>
    </a-transfer>
  </div>
</template>
<script>
import { computed, defineComponent, ref } from "vue";
const tData = [
  {
    key: 1,
    title: "系统监控",
  },
  {
    key: 2,
    title: "订单管理",
  },
  {
    key: 3,
    title: "任务管理",
  },
  {
    key: 4,
    title: "配置管理",
    children: [
      {
        key: 5,
        title: "存储管理",
      },
      {
        key: 6,
        title: "数据集管理",
      },
    ],
  },
];
const transferDataSource = [];

function flatten(list = []) {
  list.forEach((item) => {
    transferDataSource.push(item);
    flatten(item.children);
  });
}
flatten(JSON.parse(JSON.stringify(tData)));

function isChecked(selectedKeys, eventKey) {
  return selectedKeys.indexOf(eventKey) !== -1;
}

function handleTreeData(data, targetKeys = []) {
  data.forEach((item) => {
    item["disabled"] = targetKeys.includes(item.key);
    if (item.children) {
      handleTreeData(item.children, targetKeys);
    }
  });
  return data;
}
function handleTreeDataRight(data, targetKeys, arr) {
  for (let i = 0; i < data.length; i++) {
    if (targetKeys.includes(data[i].key)) {
      arr[arr.length] = {
        key: data[i].key,
        title: data[i].title,
      };
    }
    if (targetKeys.includes(data[i].key) && data[i].children) {
      arr[arr.length - 1].children = [];
      handleTreeDataRight(
        data[i].children,
        targetKeys,
        arr[arr.length - 1].children
      );
    }
  }
}
// function unique(arr) {
//   var arr1 = [];
//   for (var i = 0, len = arr.length; i < len; i++) {
//     if (arr1.indexOf(arr[i]) === -1) {
//       arr1.push(arr[i]);
//     }
//   }
//   return arr1;
// }
export default defineComponent({
  setup() {
    const selectedKeyAll = ref();
    const targetKeys = ref([1, 2, 3, 4]); //右侧选中的值
    const checkedKeysone = ref([1, 2, 3, 4]); //左侧默认勾选的值
    const dataSource = ref(transferDataSource);
    const treeData = computed(() => {
      return handleTreeData(tData, targetKeys.value);
    });
    const onChange = (keys) => {
      console.log(keys);
      targetKeys.value = keys;
      checkedKeysone.value = keys;
      checkedKey.value = [];
    };
    const onChecked = (_, e, checkedKeys, onItemSelect) => {
      console.log(e.node.eventKey);
      console.log(checkedKeys);
      const { eventKey } = e.node;
      onItemSelect(eventKey, !isChecked(checkedKeys, eventKey));
    };
    //------------------------------------------------------------------
    const checkedKey = ref([]);
    const onCheckedRight = (_, e, checkedKeys, onItemSelect) => {
      const { eventKey } = e.node;
      console.log(checkedKey.value.checked.length);
      if (checkedKey.value.checked.length == 0) checkedKey.value = [];
      console.log(eventKey);
      console.log(targetKeys);
      onItemSelect(eventKey, checkedKey.value.checked.length > 0);
    };

    const rightTreeData = computed(() => {
      let arr = [];
      handleTreeDataRight(tData, targetKeys.value, arr);
      return arr;
    });
    return {
      targetKeys,
      dataSource,
      treeData,
      onChange,
      onChecked,
      rightTreeData,
      onCheckedRight,
      checkedKey,
      selectedKeyAll,
      checkedKeysone,
    };
  },
});
</script>
